<template>
  <nav>
    <v-toolbar flat>
      <v-toolbar-items>
        <router-link to="/empInfo">
          <v-btn>איוש שעות</v-btn>
        </router-link>
        <router-link to="/test">
          <v-btn>רפורמות עובד</v-btn>
        </router-link>
        <router-link to="/HireEmp">
          <v-btn>עובדים</v-btn>
        </router-link>
        <router-link to="/mossadot">
          <v-btn>ניהול מוסדות</v-btn>
        </router-link>
        <router-link to="">
          <v-btn>דוחות</v-btn>
        </router-link>
        <router-link to="/ImportData">
          <v-btn>ניהול Excel</v-btn>
        </router-link>
        <router-link to="">
          <v-btn>הרשאות</v-btn>
        </router-link>
        <!-- <router-link to="/test">
          <v-btn>test</v-btn>
        </router-link>-->
      </v-toolbar-items>

      <v-spacer></v-spacer>
      <p>{{_mossadName}}</p>
      <v-btn v-if="_mossadName != null" @click="logout()" color="warning">התנתקות</v-btn>
    </v-toolbar>
    <v-row></v-row>
  </nav>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      mossadName: "",
    };
  },
  created() {
    this.getMossadDescription();
  },
  computed: {
    _mossadName() {
      return this.$store.state.mossadInfo.mossadName;
    },
  },
  methods: {
    getMossadDescription() {
      axios
        .get("mossadot/byId", {
          params: {
            mossadId: this.$store.state.mossadId,
          },
        })
        .then((response) => {
          axios
            .get("mossadot/byId", {
              params: {
                mossadId: this.$store.state.mossadId,
              },
            })
            .then((response) => {
              this.$store.state.mossadInfo = response.data;
              this.$store.commit("setMossadInfo", response.data);
            })
            .catch((error) => console.log(error));
          this.mossadName = response.data.mossadName;
        })
        .catch((error) => {
          // eslint-disable-next-line no-debugger
          debugger
          console.log(error)
        });
    },
    logout() {
      this.$store.dispatch("destroyToken").then(() => {
        localStorage.removeItem("mossadId");
        this.$router.push({ name: "login" });
      });
    },
  },
};
</script>

<style scoped>
nav {
  margin: 1%;
}
p {
  margin-left: 10px;
  margin-top: 15px;
  font-weight: bold;
}
</style>
