<template>
  <v-app class="rtl">
    <myNavBar />
    <v-content>
      <router-view></router-view>
    </v-content>
  </v-app>
</template>

<script>
import myNavBar from "./components/my-navbar.vue";
import axios from "axios";
export default {
  name: "App",
  components: {
    myNavBar,
  },
  data: () => ({
    //
  }),
  created() {
    this.onStart();
  },
  methods: {
    onStart() {
      if (this.$store.state.token != null) {
        axios.defaults.headers.Authorization =
          "Bearer " + this.$store.state.token;
      }
    },
  },
};
</script>

<style>
* {
  direction: rtl;
}
button {
  margin-left: 2%;
  max-height: 30px;
}
</style>
